<template>
  <view class="func-card-out" v-if="dataList.length > 0">
    <view class="func-card">
      <view class="card-header">
        <image v-if="menuIcon" class="card-header-image" :src="menuIcon" mode="scaleToFill" />
        <text class="card-header-title">{{ menuTitle }}</text>
      </view>
      <view class="card-container">
        <uv-grid :border="false" :col="4">
          <uv-grid-item
            v-for="(baseListItem, baseListIndex) in dataList"
            :key="baseListIndex"
            @click="handleClick(baseListItem)"
            :custom-style="{
              marginBottom: '32rpx',
            }"
          >
            <uv-icon :name="baseListItem.img" :size="44"></uv-icon>
            <view v-if="todoCenterData[baseListItem.key]" class="card-icon-text">
              {{ todoCenterData[baseListItem.key] }}
            </view>
            <text class="text-tip">{{ baseListItem.title }}</text>
          </uv-grid-item>
        </uv-grid>
      </view>
    </view>
  </view>
</template>

<script>
import { useUserStore } from '@/store/user'
export default {
  name: 'funcCard',
  props: {
    dataConfig: {
      type: Object,
      default: () => {},
    },
    title: {
      type: String,
      default: '',
    },
  },
  data() {
    return {}
  },
  computed: {
    menuTitle() {
      return this.dataConfig?.menuConfig?.title || ''
    },
    menuIcon() {
      return this.dataConfig?.menuConfig?.icon || ''
    },
    dataList() {
      return this.dataConfig?.menuConfig?.list || []
    },
    todoCenterData() {
      return useUserStore().userTodoCenterData
    },
  },
  methods: {
    handleClick(item) {
      if (item.value == 'auth') {
        if (this.$store.user.userInfo.is_auth_instructor == 1) {
          uni.navigateTo({
            url: '/pages-sub/realname/detail',
          })
        } else {
          uni.navigateTo({
            url: '/pages-sub/realname/index',
          })
        }
        return
      }
      if (item.value || item.path) {
        uni.navigateTo({
          url: item.value || item.path,
        })
        return
      }
      if (!item.value) {
        uni.showToast({
          title: '敬请期待:' + item.title,
          icon: 'none',
        })
      }
    },
  },
}
</script>

